@import '../../globalVariable';

.content {
  text-align: center;
  padding: 28px 18px 0;
  font-size: 15px;
  color: #353535;
}
.separator {
  height:27px;
  border-bottom: 1px solid rgba(229,229,229,1);
}

.operate {
  height: 50px;
  display: flex;
  align-items: center;
  justify-content: space-between;
  .button {
    flex: 1;
    text-align: center;
    font-size:18px;
    font-family:PingFangSC-Regular;
    font-weight:400;
    &.main {
      color: $yxLightGreen
    }
  }
  .vertical {
    flex-basis: 1px;
    height: 50px;
    border-left: 1px solid rgba(229,229,229,1);
  }
}