@charset "UTF-8";
:global .rc-dialog {
  position: relative;
  width: 280px;
  margin: 10px auto;
  top: 50%;
  transform: translateY(-70%); }
  :global .rc-dialog-wrap {
    position: fixed;
    overflow: auto;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    z-index: 1050;
    -webkit-overflow-scrolling: touch;
    outline: 0; }
  :global .rc-dialog-title {
    margin: 0;
    font-size: 18px;
    line-height: 25px;
    font-weight: 500;
    color: #000000;
    text-align: center;
    font-family: PingFangSC-Medium; }
  :global .rc-dialog-content {
    position: relative;
    background-color: #ffffff;
    border: none;
    border-radius: 8px;
    background-clip: padding-box; }
  :global .rc-dialog-close {
    cursor: pointer;
    border: 0;
    background: transparent;
    font-size: 21px;
    position: absolute;
    right: 20px;
    top: 12px;
    font-weight: 700;
    line-height: 1;
    color: #000;
    text-shadow: 0 1px 0 #fff;
    filter: alpha(opacity=20);
    opacity: .2;
    text-decoration: none; }
    :global .rc-dialog-close-x:after {
      content: '×'; }
    :global .rc-dialog-close:hover {
      opacity: 1;
      filter: alpha(opacity=100);
      text-decoration: none; }
  :global .rc-dialog-header {
    padding: 12px 20px 12px 20px;
    border-radius: 5px 5px 0 0;
    background: #fff;
    color: #666;
    border-bottom: 1px solid #e9e9e9; }
  :global .rc-dialog-body {
    padding: 0;
    font-family: PingFangSC-Regular; }
  :global .rc-dialog-footer {
    border-top: 1px solid #e9e9e9;
    padding: 10px 20px;
    text-align: right;
    border-radius: 0 0 5px 5px; }
  :global .rc-dialog-zoom-enter, :global .rc-dialog-zoom-appear {
    opacity: 0;
    animation-duration: 0.3s;
    animation-fill-mode: both;
    animation-timing-function: cubic-bezier(0.08, 0.82, 0.17, 1);
    animation-play-state: paused; }
  :global .rc-dialog-zoom-leave {
    animation-duration: 0.3s;
    animation-fill-mode: both;
    animation-timing-function: cubic-bezier(0.6, 0.04, 0.98, 0.34);
    animation-play-state: paused; }
  :global .rc-dialog-zoom-enter :global .rc-dialog-zoom-enter-active, :global .rc-dialog-zoom-appear :global .rc-dialog-zoom-appear-active {
    animation-name: rcDialogZoomIn;
    animation-play-state: running; }
  :global .rc-dialog-zoom-leave :global .rc-dialog-zoom-leave-active {
    animation-name: rcDialogZoomOut;
    animation-play-state: running; }

@keyframes rcDialogZoomIn {
  0% {
    opacity: 0;
    transform: scale(0, 0); }
  100% {
    opacity: 1;
    transform: scale(1, 1); } }

@keyframes rcDialogZoomOut {
  0% {
    transform: scale(1, 1); }
  100% {
    opacity: 0;
    transform: scale(0, 0); } }
  :global .rc-dialog-mask {
    position: fixed;
    top: 0;
    right: 0;
    left: 0;
    bottom: 0;
    background-color: #373737;
    background-color: rgba(55, 55, 55, 0.6);
    height: 100%;
    filter: alpha(opacity=50);
    z-index: 1050; }
    :global .rc-dialog-mask-hidden {
      display: none; }
  :global .rc-dialog-fade-enter, :global .rc-dialog-fade-appear {
    opacity: 0;
    animation-duration: 0.3s;
    animation-fill-mode: both;
    animation-timing-function: cubic-bezier(0.55, 0, 0.55, 0.2);
    animation-play-state: paused; }
    :global .rc-dialog-fade-enter-active, :global .rc-dialog-fade-appear-active {
      animation-name: rcDialogFadeIn;
      animation-play-state: running; }
  :global .rc-dialog-fade-leave {
    animation-duration: 0.3s;
    animation-fill-mode: both;
    animation-timing-function: cubic-bezier(0.55, 0, 0.55, 0.2);
    animation-play-state: paused; }
  :global .rc-dialog-fade-leave-active {
    animation-name: rcDialogFadeOut;
    animation-play-state: running; }

@keyframes :global(rcDialogFadeIn) {
  0% {
    opacity: 0; }
  100% {
    opacity: 1; } }

@keyframes :global(rcDialogFadeOut) {
  0% {
    opacity: 1; }
  100% {
    opacity: 0; } }

@media (min-width: 768px) {
  .rc-dialog {
    width: 600px;
    margin: 30px auto; } }
